import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';


// importing images;
import img1 from '../img/1.jpg';
import img2 from '../img/2.jpg';
import img3 from '../img/3.jpg';

const useStyles = makeStyles({
  root: {
    maxWidth: 330,
    boxShadow: "5px 5px 5px #999",
    '&:hover': {
      boxShadow:"10px 10px 8px #555"
    },
    minHeight : "400px"
  },
  cardHeading:{
    fontWeight : "500"
  }
});

export default function ImgMediaCard() {
  const classes = useStyles();

  const rounds = [
    { title: "Round 1 - Consists Of Basic Digital Electronic Circuits ",            body: "Round consists of basic Digital Electronic Circuits.Find the correct solution of the given questions. Then you need to combine all these integral answers to get a password for the Second round", img: img1, time:"45 Mins"  },
    { title: "Round 2 - Questions Regarding Analog Electronics."       ,            body: "This round consists of questions regarding Analog Electronics. Find the correct solution of the given questions. Then you need to combine all these integral answers to get a password for the Third round.", img: img2, time : "60 Mins"  },
    { title: "Round 3 - Questions Mixed From Different Domains",                    body: "You need to combine all these integral answers (write one after the another) to get a password for the Final Feedback Form.It is compulsory to fill the Feedback Form.", img: img3, time:"1 Hour 15 mins"  }
  ]

  const renderRounds = () => {

    return (

      rounds.map((round) => {
        return (
          <Grid item>
            <Card className={classes.root} key={round.title}>
              <CardActionArea>
                <CardMedia
                  component="img"
                  alt={round.title}
                  height="180"
                  image={round.img}
                  title={round.title}
                />
                <CardContent>
                  <Typography className={classes.cardHeading}  gutterBottom variant="h6" component="h2">
                    {round.title}
                  </Typography>
                  <Divider className={classes.divider} style={{margin:"15px 8px"}}  />

                  <Typography variant="body2" color="textSecondary" component="p" style={{textAlign:"justify", textJustify:"inter-word"}}>
                    {round.body}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>

        )
      })
    )
  }

  return (
    <Grid container justify="center" spacing={6} >

    {renderRounds()}
    </Grid>
  );
}
